export default ()=> {
      return (
        <div className="question" data-index="67" dangerouslySetInnerHTML = {{ __html: `
<h6>67. 输出什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token comment">// index.js</span>
console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">'running index.js'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token keyword keyword-import">import</span> <span class="token punctuation">{</span> sum <span class="token punctuation">}</span> <span class="token keyword keyword-from">from</span> <span class="token string">'./sum.js'</span><span class="token punctuation">;</span>
console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token function">sum</span><span class="token punctuation">(</span><span class="token number">1</span><span class="token punctuation">,</span> <span class="token number">2</span><span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// sum.js</span>
console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">'running sum.js'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token keyword keyword-export">export</span> <span class="token keyword keyword-const">const</span> <span class="token function-variable function">sum</span> <span class="token operator">=</span> <span class="token punctuation">(</span><span class="token parameter">a<span class="token punctuation">,</span> b</span><span class="token punctuation">)</span> <span class="token operator">=></span> a <span class="token operator">+</span> b<span class="token punctuation">;</span>
</code></pre>
<ul>
<li>A: <code>running index.js</code>, <code>running sum.js</code>, <code>3</code></li>
<li>B: <code>running sum.js</code>, <code>running index.js</code>, <code>3</code></li>
<li>C: <code>running sum.js</code>, <code>3</code>, <code>running index.js</code></li>
<li>D: <code>running index.js</code>, <code>undefined</code>, <code>running sum.js</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: B</h4>
<p><code>import</code>命令是编译阶段执行的，在代码运行之前。因此这意味着被导入的模块会先运行，而导入模块的文件会后执行。</p>
<p>这是CommonJS中<code>require（）</code>和<code>import</code>之间的区别。使用<code>require()</code>，您可以在运行代码时根据需要加载依赖项。 如果我们使用<code>require</code>而不是<code>import</code>，<code>running index.js</code>，<code>running sum.js</code>，<code>3</code>会被依次打印。</p>
</p>
</details>
` }}></div>
      );
    }